import {useState} from "react";
import {saveChecklistGoal} from "../../containers/cbChecklist/checkListMethods";

const EditGoalRow = ({item,idx}) => {
    item["displayorder"]=isNaN(item.displayorder) ? idx+1 : item.displayorder;
    let [goal,setGoal] = useState(item);
    let bgcolor = goal.hasOwnProperty("isDirty") && goal.isDirty ? "yellow" : "none";

    let saveButton = goal?.isDirty ? <button className={"btn btn-sm btn-secondary"} onClick={()=>{setGoal(Object.assign({},goal,{"isDirty":false})); saveChecklistGoal(goal)}}>Save</button> : null;

    return <tr style={{backgroundColor:bgcolor}}>
        <td>{idx+1}</td>
        <td><input type={"text"} value={goal.label} onChange={(evt)=>setGoal(Object.assign({},goal,{"label":evt.currentTarget.value,isDirty:true}))} /></td>
        <td><input type={"checkbox"} checked={goal.active} onChange={(evt)=>setGoal(Object.assign({},goal,{"active":evt.currentTarget.checked,isDirty:true}))} /> </td>
        <td>{saveButton} </td>
    </tr>
}

export default EditGoalRow;